import chainIcon from "@/assets/chainIcon.png";
import gangtanhao from "@/assets/gangtanhao.png";
import Clipboard from "clipboard";
export default {
  data() {
    return {
      isInfo: true,
      topTitle: "充值",
      chainIcon: chainIcon,
      gangtanhaoIcon: gangtanhao,
      toastVAl: false,
      toastText: "",
      code: "",
      MEMOcode: "",
      list: [],
      listVal: "",
      address: "",
      note: "",
      form: {
        coin_type: "",
        chain: "",
        coin_status: "",
        minNumber: "0"
      }
    };
  },
  filters: {
    uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }
  },
  mounted() {
    this.getCoin();
  },
  methods: {
    setIsInfo() {
      this.isInfo = false;
    },
    // /获取币种币链
    getCoin() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/get-coin",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list = response.data;
          if (that.$route.params.coin_type) {
            that.form.coin_type = that.$route.params.coin_type;
          } else {
            that.form.coin_type = that.list[0].coin_type;
          }
          that.getChainCoin(that.form.coin_type);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getChainCoin(val) {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          coin_type: val
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/get-chain-coin",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.form.chain = response.data[0].chain;
          that.form.coin_status = response.data[0].coin_status;
          that.getData();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 用户充值
    getData() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          chain: that.form.chain,
          coin_type: that.form.coin_type
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/deposit-submit",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.form.minNumber = response.data.min_deposit_amount;
          that.code = response.data.addr;
          that.MEMOcode = "MEMOcode";
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    onChange(val) {
    },
    copyWord(idVal) {
      var that = this;
      var clipboard = new Clipboard(idVal);

      clipboard.on("success", function(e) {
        that.toastVAl = true;
        that.toastText = "复制成功";
        console.info("Text:", e.text);
        e.clearSelection();
      });

      clipboard.on("error", function(e) {
        console.error("Action:", e.action);
        console.error("Trigger:", e.trigger);
      });
    },
    // 币链接信息介绍
    getInfo() {
      this.$router.push("/chainCoinInfo");
    }
  }
};



